import React from 'react'
import { Typography, Grid, Container, Paper } from '@material-ui/core';
import Avatar from "@material-ui/core/Avatar";
import { makeStyles } from "@material-ui/core";
import logo from "../resources/logo_big.png";
import AddBoxRoundedIcon from '@material-ui/icons/AddBoxRounded';
import GroupRoundedIcon from '@material-ui/icons/GroupRounded';

const useStyles = makeStyles((theme) => {
    return {
        heading: {
            marginBottom: '0',
            fontWeight: "600"
        },
        hr: {
            minWidth: '340px',
        },
        icon: {
            fontSize: 40,
            margin: "10px"
        },
        teams: {
            display: "flex",
        },
        team: {
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
            minWidth: "100px",
            backgroundColor: "transparent"
        },
        task: {
            display: "flex",
            alignItems: "center",
            flexDirection: "row",
            minWidth: "100px",
            backgroundColor: "transparent"
        }
    }
})

export default function DivisionDetails() {
    const classes = useStyles()

    return (
        <Grid container style={{ padding: 8 }}>
            <Grid item md={6} lg={6}>
                <Typography variant="body1" className={classes.heading}>Division <hr className={classes.hr} /> </Typography>
                <Typography variant="body2">General Adinistration </Typography>
                <br />
                <Typography variant="body1" className={classes.heading}>Head of Division <hr className={classes.hr} /> </Typography>
                <Grid container>
                    <Grid Item xs={4}>
                        <Avatar alt="Remy Sharp" src={logo} />
                    </Grid>
                    <Grid Item xs={8} style={{ marginTop: "10px" }}>
                        <Typography variant="body2">A.T. Pathirana</Typography>
                    </Grid>
                </Grid>
                <br />
                <Typography variant="body1" className={classes.heading}>Teams <hr className={classes.hr} /> </Typography>
                <Container className={classes.teams}>
                    <Paper elevation={0} className={classes.team}>
                        <AddBoxRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Add Team</Typography>
                    </Paper>
                    <Paper elevation={0} className={classes.team}>
                        <GroupRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Web Team</Typography>
                    </Paper>
                    <Paper elevation={0} className={classes.team}>
                        <GroupRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Doc Team</Typography>
                    </Paper>
                </Container>
                <br />
                <Typography variant="body1" className={classes.heading}>Projects/ Tasks <hr className={classes.hr} /> </Typography>
                <Container className={classes.tasks}>
                    <Paper elevation={0} className={classes.task}>
                        <AddBoxRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Add task</Typography>
                    </Paper>
                    <Paper elevation={0} className={classes.task}>
                        <GroupRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Add task</Typography>
                    </Paper>
                    <Paper elevation={0} className={classes.task}>
                        <GroupRoundedIcon className={classes.icon} color="primary" />
                        <Typography variant="body2">Add task</Typography>
                    </Paper>
                </Container>
            </Grid>

            <Grid item md={6} lg={6}>
                <Typography variant="body1" pb={1} >

                </Typography>
            </Grid>
        </Grid >
    )
}
//className={classes.data}